/* === Retro Arcade Theme (full file) ===================================== */
@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&display=swap");

:root {
  --arcade-bg-1: #0b0f1a;
  --arcade-bg-2: #121a2b;
  --arcade-neon-cyan: #6df3ff;
  --arcade-neon-pink: #ff57d2;
  --arcade-neon-yellow: #ffe66d;
  --arcade-neon-red: #ff7a7a;
  --arcade-ink: #e9f5ff;
  --arcade-muted: #9fb0c0;
}

body.theme-arcade {
  background:
    linear-gradient(180deg, rgba(6,10,20,.55), rgba(6,10,20,.85)),
    radial-gradient(1200px 600px at 20% -10%, rgba(255,87,210,.12), transparent 60%),
    radial-gradient(900px 500px at 110% 10%, rgba(109,243,255,.12), transparent 60%),
    url("./assets/bg.jpg") center/cover fixed no-repeat;
  color: var(--arcade-ink);
  font-family: "VT323", ui-monospace, Menlo, Consolas, monospace;
  letter-spacing: .2px;
}

body.theme-arcade::before {
  content: "";
  position: fixed; inset: 0; pointer-events: none;
  background: repeating-linear-gradient(180deg,
    rgba(255,255,255,.035) 0px, rgba(255,255,255,.035) 1px,
    transparent 2px, transparent 3px);
  opacity: .35; mix-blend-mode: soft-light;
}

body.theme-arcade .app-title,
body.theme-arcade h1 {
  font-family: "Press Start 2P", system-ui, sans-serif;
  font-size: 1.05rem;
  letter-spacing: 1px;
  color: var(--arcade-neon-cyan);
  text-shadow: 0 0 6px rgba(109,243,255,.8), 0 0 18px rgba(109,243,255,.45);
}
body.theme-arcade .version-badge {
  color: var(--arcade-muted);
  background: rgba(255,255,255,.04);
  border: 1px solid rgba(255,255,255,.12);
  border-radius: 6px; padding: 2px 8px; margin-left: 8px;
}

body.theme-arcade .editor-content,
body.theme-arcade .help-card,
body.theme-arcade .panel,
body.theme-arcade .box {
  background: rgba(8,13,25,.6);
  border: 1px solid rgba(109,243,255,.28);
  border-radius: 10px;
  box-shadow:
    0 0 0 1px rgba(109,243,255,.15) inset,
    0 8px 24px rgba(0,0,0,.45),
    0 0 22px rgba(109,243,255,.15);
}

body.theme-arcade select,
body.theme-arcade .btn,
body.theme-arcade button,
body.theme-arcade .toggle {
  color: var(--arcade-ink);
  background: rgba(255,255,255,.03);
  border: 2px solid var(--arcade-neon-cyan);
  border-radius: 10px;
  padding: 8px 12px;
  box-shadow: 0 0 10px rgba(109,243,255,.25), inset 0 0 10px rgba(109,243,255,.12);
  transition: transform .06s ease, box-shadow .12s ease, filter .1s ease;
}
body.theme-arcade button { border-color: var(--arcade-neon-pink) }
body.theme-arcade button:hover,
body.theme-arcade .btn:hover,
body.theme-arcade select:hover {
  transform: translateY(-1px);
  filter: brightness(1.1);
  box-shadow: 0 0 14px rgba(109,243,255,.35), 0 0 24px rgba(109,243,255,.15);
}

/* High-contrast dropdown list items */
body.theme-arcade select option {
  color: var(--arcade-ink);
  background: #0b0f1a;
}
body.theme-arcade select option:disabled {
  color: #708090;
}

/* Pills */
body.theme-arcade .bg-gray-100,
body.theme-arcade .bg-green-100,
body.theme-arcade .bg-blue-100 {
  background: rgba(255,255,255,.06) !important;
  border: 1px solid rgba(255,255,255,.12);
  color: var(--arcade-ink);
  border-radius: 999px !important;
}

/* Legend squares */
body.theme-arcade .legend-yellow,
body.theme-arcade .legend-red,
body.theme-arcade .legend-blue {
  width: 14px; height: 14px; border-radius: 3px; display: inline-block;
  border: 1px solid rgba(255,255,255,.2);
  box-shadow: 0 0 10px currentColor;
}
body.theme-arcade .legend-yellow { background: #2b2600; color: var(--arcade-neon-yellow); box-shadow: 0 0 20px #ffe66d90; }
body.theme-arcade .legend-red    { background: #2b0000; color: var(--arcade-neon-red);    box-shadow: 0 0 20px #ff7a7a90; }
body.theme-arcade .legend-blue   { background: #001b2b; color: #7cc3ff;                  box-shadow: 0 0 20px #7cc3ff90; }

/* Hard word legend sample */
body.theme-arcade .legend-underline-purple {
  text-decoration: underline dotted var(--arcade-neon-pink);
  text-underline-offset: 3px; font-weight: 700;
}

/* Tooltip */
body.theme-arcade #ss-tooltip {
  background: rgba(12,18,32,.9) !important;
  color: var(--arcade-ink) !important;
  border: 1px solid rgba(109,243,255,.35) !important;
  border-radius: 8px !important;
  box-shadow: 0 0 16px rgba(109,243,255,.25), 0 6px 26px rgba(0,0,0,.6) !important;
}

/* === Highlights ========================================================== */
body.theme-arcade mark[data-highlight-color] {
  color: #ffffff !important;
  font-weight: 600;
}
body.theme-arcade mark[data-highlight-color="yellow"] {
  background-color: rgba(255,230,109,.28);
  border: 2px solid #ffe66d; border-radius: 3px; padding: 0 .12em;
  box-shadow: 0 0 20px #ffe66db0;
}
body.theme-arcade mark[data-highlight-color="red"] {
  background-color: rgba(255,122,122,.28);
  border: 2px solid #ff7a7a; border-radius: 3px; padding: 0 .12em;
  box-shadow: 0 0 22px #ff7a7ab0;
}
body.theme-arcade mark[data-highlight-color="blue"] {
  background-color: rgba(124,195,255,.24);
  border: 2px solid #7cc3ff; border-radius: 3px; padding: 0 .12em;
  box-shadow: 0 0 20px #7cc3ffb0;
}

/* Purple underline for hard words */
body.theme-arcade .editor-content span[data-hard-word="1"] {
  text-decoration: underline dotted var(--arcade-neon-pink);
  text-decoration-thickness: 2px; text-underline-offset: 2px;
  filter: drop-shadow(0 0 8px rgba(255,87,210,.8));
}

/* Help accordion and prompt color */
body.theme-arcade .help-summary {
  background: rgba(255,255,255,.05); color: var(--arcade-ink);
  font-family: "Press Start 2P", system-ui, sans-serif; font-size: .8rem;
}
body.theme-arcade .help-card[open] .help-summary { border-bottom: 1px solid rgba(109,243,255,.28); }
body.theme-arcade .help-pre { color: #0f172a; }

/* === Screen FX (Arcade only) ============================================ */
body.theme-arcade.arcade-shake-big .editor-content { animation: fx-shake-big 110ms ease-in-out 0s 8; }
body.theme-arcade.arcade-shake-small .editor-content { animation: fx-shake-small 120ms ease-in-out 0s 5; }

@keyframes fx-shake-big {
  0%   { transform: translate(0, 0) rotate(0deg); }
  20%  { transform: translate(8px, -8px) rotate(-0.4deg); }
  40%  { transform: translate(-10px, 6px) rotate(0.4deg); }
  60%  { transform: translate(7px, 9px) rotate(-0.5deg); }
  80%  { transform: translate(-8px, -6px) rotate(0.5deg); }
  100% { transform: translate(0, 0) rotate(0deg); }
}
@keyframes fx-shake-small {
  0%   { transform: translate(0, 0); }
  25%  { transform: translate(4px, -3px); }
  50%  { transform: translate(-4px, 3px); }
  75%  { transform: translate(3px, 4px); }
  100% { transform: translate(0, 0); }
}

body.theme-arcade.arcade-flash .editor-content { animation: fx-flash 700ms ease-out; }
@keyframes fx-flash {
  0% {
    box-shadow:
      0 0 0 4px var(--arcade-neon-pink),
      0 0 36px rgba(255,87,210,.75),
      0 0 120px rgba(255,87,210,.45);
    filter: brightness(1.25);
  }
  100% { box-shadow: none; filter: none; }
}

body.theme-arcade.arcade-glow .editor-content { animation: fx-glow 800ms ease-in-out; }
@keyframes fx-glow {
  0% {
    transform: scale(1.012);
    box-shadow:
      0 0 0 4px var(--arcade-neon-cyan),
      0 0 44px rgba(109,243,255,.85),
      0 0 120px rgba(109,243,255,.45);
    filter: saturate(1.2);
  }
  60% {
    transform: scale(1.004);
    box-shadow:
      0 0 0 2px rgba(109,243,255,.5),
      0 0 24px rgba(109,243,255,.5),
      0 0 60px rgba(109,243,255,.25);
  }
  100% { transform: none; box-shadow: none; filter: none; }
}

.fx-overlay { position: fixed; inset: 0; z-index: 9999; pointer-events: none; display: grid; place-items: center; }
body.theme-arcade .fx-overlay {
  background:
    radial-gradient(40% 40% at 50% 45%, rgba(109,243,255,.10), transparent 60%),
    radial-gradient(75% 75% at 50% 55%, rgba(0,0,0,.25), transparent 75%);
}
.fx-banner {
  font-family: "Press Start 2P", system-ui, sans-serif;
  font-size: 22px;
  letter-spacing: 2px;
  padding: 14px 18px;
  color: var(--arcade-neon-cyan);
  background: rgba(8,13,25,.92);
  border: 2px solid var(--arcade-neon-cyan);
  border-radius: 12px;
  text-shadow: 0 0 10px rgba(109,243,255,.8);
  box-shadow:
    0 0 22px rgba(109,243,255,.35),
    inset 0 0 16px rgba(109,243,255,.15);
}
.fx-confetti-wrap { position: fixed; inset: 0; overflow: hidden; }
.fx-piece {
  position: absolute;
  top: -10%;
  width: 10px; height: 14px;
  border-radius: 2px;
  box-shadow: 0 0 10px rgba(255,255,255,.2);
  animation-name: fx-fall, fx-spin;
  animation-timing-function: linear, ease-in-out;
}
.fx-piece:nth-child(3n)     { background: #6df3ff; }
.fx-piece:nth-child(3n + 1) { background: #ff57d2; }
.fx-piece:nth-child(3n + 2) { background: #ffe66d; }
.fx-piece:nth-child(5n)     { background: #7cc3ff; }

@keyframes fx-fall { to { transform: translateY(120vh) rotate(360deg); opacity: 0.95; } }
@keyframes fx-spin { 50% { border-radius: 6px; } }

body.theme-arcade *:focus-visible {
  outline: 2px solid var(--arcade-neon-cyan);
  outline-offset: 2px;
  box-shadow: 0 0 8px rgba(109,243,255,.65);
}
