/* Real Readability feature styles (scoped, non-invasive) */

.ss-container {
  max-width: 864px;
  margin: 0 auto;
  padding: 0 8px;
}

.ss-appbar {
  display: flex;
  align-items: flex-start; /* allow subline under the title */
  gap: 12px;
  padding: 10px 0;
}

/* Title block with subline under it */
.ss-titleblock {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-right: auto; /* push rest to the right */
}

.ss-title {
  font-weight: 700;
}

/* subline (target + version) under the title, subdued */
.ss-subline {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  color: #6b7280;
  font-size: 12px;
}

.ss-target {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  color: #374151;
  font-size: 12px;
}
.ss-target select {
  border: 1px solid #d1d5db;
  border-radius: 8px;
  padding: 2px 6px;
  background: #fff;
  font-size: 12px;
}
.ss-version {
  font-size: 12px;
  color: #6b7280;
}

.ss-btn {
  border: 1px solid #111827;
  background: #111827;
  color: #fff;
  padding: 8px 12px;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
}
.ss-btn:hover { opacity: 0.92; }
.ss-btn.secondary { background: #fff; color: #111827; border-color: #c7cbd1; }

.ss-inline-controls { display: flex; align-items: center; gap: 8px; }

.ss-main {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  position: relative;
}
.ss-main.ss-with-panel { grid-template-columns: minmax(0, 1fr) 320px; }

.ss-editor-col { position: relative; min-height: 300px; }
.ss-panel-col { display: block; }

/* ----- Header pills (Real scores while panel open) ----- */
.ssr-real-scores {
  display: inline-flex;
  align-items: center;
  gap: 8px;
}
.ssr-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 6px 10px;
}
.ssr-badge-label { color: #4b5563; font-size: 12px; }
.ssr-badge-value { font-weight: 700; font-variant-numeric: tabular-nums; }

.ssr-linkbtn {
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #2563eb;
  padding: 6px 10px;
  border-radius: 10px;
  font-size: 13px;
  cursor: pointer;
}
.ssr-linkbtn:hover { background: #f3f4f6; }

/* Close right next to the hidden words pill */
.ssr-closebtn {
  border: 1px solid #e5e7eb;
  background: #fff;
  color: #111827;
  padding: 6px 10px;
  border-radius: 10px;
  font-size: 13px;
  cursor: pointer;
}
.ssr-closebtn:hover { background: #f3f4f6; }

/* ----- Panel (inside the grid) ----- */
.ss-sidepanel.in-flow {
  position: sticky;
  top: 0;
  align-self: start;
  max-height: calc(100vh - 24px);
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  padding: 12px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.ss-sidepanel-header h3 { margin: 0 0 4px 0; font-size: 16px; }
.ss-muted { color: #6b7280; font-size: 13px; margin: 0 0 8px 0; }

.ss-controls { display: flex; flex-direction: column; gap: 10px; }
.ss-row { display: flex; align-items: center; gap: 8px; }

.ss-note {
  margin-top: 8px;
  background: #fef3c7;
  border: 1px solid #fde68a;
  color: #92400e;
  padding: 6px 8px;
  border-radius: 6px;
  font-size: 12px;
}

.ss-hint { color: #6b7280; font-size: 12px; }

/* Simple 2-col table */
.ss-simple-table { border: 1px solid #e5e7eb; border-radius: 8px; overflow: hidden; }
.ss-simple-header {
  display: grid; grid-template-columns: 1fr 70px;
  background: #f3f4f6; color: #4b5563; font-size: 12px;
  padding: 8px 10px; gap: 8px;
}
.ss-simple-body { max-height: 50vh; overflow: auto; }
.ss-simple-row {
  display: grid; grid-template-columns: 1fr 70px;
  padding: 8px 10px; gap: 8px; align-items: center;
  border-top: 1px solid #f3f4f6;
}
.ss-simple-empty { padding: 12px; color: #6b7280; font-size: 13px; }
.ss-word { font-size: 14px; }
.ss-xbtn {
  border: 1px solid #e5e7eb; background: #fff; border-radius: 8px;
  padding: 4px 8px; cursor: pointer;
}
.ss-xbtn:hover { background: #f3f4f6; }

/* Mute existing highlights while panel is open */
.ss-marking-mode .ProseMirror [class*="highlight"],
.ss-marking-mode .ProseMirror [class*="underline"] {
  opacity: 0.18 !important;
  pointer-events: none !important;
}

/* -------- Arcade fallback: hide the Get Real button even if context fails -------- */
body.ss-arcade .ss-btn-getreal,
html.ss-arcade .ss-btn-getreal,
.ss-arcade .ss-btn-getreal {
  display: none !important;
}
