/* =========================================================
   Sentence Scratcher — Custom CSS (full file)
   ========================================================= */

/* ---------- App header bits ---------- */
.app-title { font-weight: 800; }
.version-badge { font-size: 12px; padding: 2px 6px; border: 1px solid #e5e7eb; border-radius: 6px; }

/* ---------- Target grade (under title, left) ---------- */
.grade-row { margin-top: 2px; }
.grade-label { font-size: 0.95rem; font-weight: 600; color: #111827; }
.grade-select {
  padding: 6px 10px;
  border: 1px solid #cbd5e1;
  border-radius: 10px;
  background: #fff;
  color: #111827;
  outline: none;
}
.grade-select:focus { box-shadow: 0 0 0 3px rgba(59,130,246,.25); border-color: #93c5fd; }

/* ---------- Classic editor look ---------- */
.editor-content {
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, Noto Sans, "Apple Color Emoji", "Segoe UI Emoji";
  line-height: 1.6;
  color: #111827;
  min-height: 320px;
}
.editor-content p { margin: 0.5rem 0; }
.editor-content h1 { font-size: 1.75rem; line-height: 1.25; font-weight: 800; margin: 1.25rem 0 0.75rem; }
.editor-content h2 { font-size: 1.5rem;  line-height: 1.3;  font-weight: 700; margin: 1rem 0 0.5rem; }
.editor-content h3 { font-size: 1.25rem; line-height: 1.35; font-weight: 700; margin: 0.85rem 0 0.4rem; }
.editor-content h4 { font-size: 1.125rem;line-height: 1.4;  font-weight: 600; margin: 0.75rem 0 0.35rem; }
.editor-content ul { list-style: disc; padding-left: 1.5rem; margin: 0.5rem 0; }
.editor-content ol { list-style: decimal; padding-left: 1.5rem; margin: 0.5rem 0; }
.editor-content li { margin: 0.25rem 0; }

/* ---------- Legend chips & samples ---------- */
.legend-yellow { background-color: #fff3b0; }
.legend-red    { background-color: #ffd1d1; }
.legend-blue   { background-color: #dbeafe; }

/* higher-contrast dashed sample line (classic + arcade) */
.legend-underline-gray {
  display: inline-block;
  border-bottom: 2px dashed #5a67d8;  /* slate-ish blue */
  width: 36px;
  height: 0;
  vertical-align: middle;
}

.legend-underline-purple,
[data-hard-word="1"] {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-color: #7c3aed;
  text-decoration-thickness: 2px;
  text-underline-offset: 2px;
}

/* ---------- NEW: legend layout to prevent overlap ---------- */
.legend-list { padding-left: 1.25rem; }
.legend-item { 
  display: flex; 
  align-items: baseline; 
  gap: 8px; 
  flex-wrap: wrap;          /* allow description to wrap below on narrow widths */
}
.legend-pill {
  display: inline-block;
  white-space: nowrap;      /* keep 'Hard to read' as a unit */
  padding: 0 6px;
  line-height: 1.25;
  border-radius: 4px;
}
.legend-desc { 
  flex: 1 1 auto; 
  min-width: 10ch;          /* ensures there’s room before wrapping */
}

/* ---------- Continuous, no-break sentence highlights ---------- */
mark[data-highlight-color] {
  padding: 0 0.08em;
  border: none;
  border-radius: 2px;
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
}
mark[data-highlight-color="yellow"] { background-color: #fff3b0; }
mark[data-highlight-color="red"]    { background-color: #ffd1d1; }
mark[data-highlight-color="blue"]   { background-color: #dbeafe; }

/* ---------- Tooltip bubble (created in JS as #ss-tooltip) ---------- */
#ss-tooltip {
  position: fixed; z-index: 2147483647; max-width: 28rem;
  padding: 6px 8px; font-size: 12px; line-height: 1.25;
  color: #111827; background: #f9fafb; border: 1px solid #e5e7eb;
  border-radius: 4px; box-shadow: 0 4px 10px rgba(0,0,0,.12);
  pointer-events: none; display: none; will-change: transform;
}

/* ---------- Help card (classic theme) ---------- */
.help-card { border: 1px solid #e5e7eb; border-radius: 10px; background: #ffffff; overflow: hidden; }
.help-summary { cursor: pointer; padding: 14px 16px; font-weight: 800; list-style: none; background: #f9fafb; }
.help-body { padding: 16px; }
.help-section { margin-bottom: 16px; }
.help-card, .help-body, .help-section, .help-list, .help-list li, .help-body p { white-space: normal; }

.help-body h3 { font-size: 1.05rem; font-weight: 800; margin: 0 0 8px; }
.help-list { list-style: disc; margin: 0.35rem 0 0; }
.help-list li { margin: 0.25rem 0; }

.help-pre {
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  background: #0b1020;
  color: #e5e7eb;
  padding: 0.85rem 1rem;
  border-radius: 8px;
  line-height: 1.45;
  white-space: pre-wrap;
  word-break: break-word;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 18rem;
  border: 1px solid #1118270f;
  box-shadow: inset 0 0 0 1px rgba(255,255,255,0.04);
}

.help-actions { margin-top: 8px; }
.help-actions .btn {
  padding: 6px 10px;
  border: 1px solid #cbd5e1;
  border-radius: 6px;
  background: #fff;
  font-weight: 600;
}
.help-note { font-size: 12px; color: #475569; margin-top: 6px; }

/* =========================================================
   ARCADE THEME OVERRIDES
   ========================================================= */
body.theme-arcade .editor-content {
  font-family: "VT323","Pixelify Sans","Share Tech Mono",monospace !important;
  font-size: 16px !important; line-height: 1.45 !important; color: #ffffff !important;
}
body.theme-arcade mark[data-highlight-color="yellow"] { background-color: rgba(255,230,0,.20); }
body.theme-arcade mark[data-highlight-color="red"]    { background-color: rgba(255,91,91,.22); }
body.theme-arcade mark[data-highlight-color="blue"]   { background-color: rgba(80,150,255,.22); }

/* Arcade-styled grade control */
body.theme-arcade .grade-label { color: #dff7ff; font-family: "VT323","Pixelify Sans","Share Tech Mono",monospace; }
body.theme-arcade .grade-select {
  background: rgba(12,16,28,.65); color: #dff7ff; border: 1px solid rgba(120,255,255,.6);
  box-shadow: inset 0 0 24px rgba(0,255,255,.10), 0 0 0 2px rgba(120,255,255,.25);
}
body.theme-arcade .grade-select:focus {
  outline: none; box-shadow: inset 0 0 24px rgba(0,255,255,.18), 0 0 0 2px rgba(120,255,255,.45);
  border-color: rgba(120,255,255,.9);
}

/* Arcade help card tweaks */
body.theme-arcade .help-card {
  background: rgba(10,14,28,.65);
  border-color: rgba(120,255,255,.25);
  box-shadow: inset 0 0 0 1px rgba(120,255,255,.12);
}
body.theme-arcade .help-summary { background: rgba(10,14,28,.85); color: #dff7ff; }
body.theme-arcade .help-body,
body.theme-arcade .help-body p,
body.theme-arcade .help-body h3,
body.theme-arcade .help-list li { color: #eaf6ff; line-height: 1.6; }

/* legend flex items in arcade (no overlap, clean wrap) */
body.theme-arcade .legend-item { gap: 10px; }
body.theme-arcade .legend-pill { border-radius: 4px; }
body.theme-arcade .legend-desc { flex: 1 1 auto; min-width: 10ch; }

/* Prompt block — bright text + neon-ish frame */
body.theme-arcade .help-pre {
  background: rgba(5,10,24,.92);
  color: #ffffff !important;
  border-color: rgba(120,255,255,.35);
  box-shadow: inset 0 0 0 1px rgba(120,255,255,.28);
}

/* Arcade copy button */
body.theme-arcade .help-actions .btn {
  background: rgba(12,16,28,.9);
  color: #dff7ff;
  border: 1px solid rgba(120,255,255,.35);
}

/* Always hide any legacy/classic scoreboard */
.classic-score { display: none !important; }

/* ---------------------------------------------------------
   ARCADE TOGGLE (portal, top-right)
   --------------------------------------------------------- */
.arcade-toggle {
  position: fixed;
  top: 12px;
  right: 12px;
  z-index: 2147483600;
  padding: 10px 14px;
  border-radius: 12px;
  border: 1px solid #111827;
  background: #fff;
  color: #111827;
  font-weight: 700;
  cursor: pointer;
  transition: transform .12s ease, box-shadow .12s ease;
}
.arcade-toggle:hover { transform: translateY(-1px); box-shadow: 0 4px 12px rgba(0,0,0,.15); }
body.theme-arcade .arcade-toggle {
  border: 1px solid rgba(120,255,255,.6);
  color: #dff7ff;
  background: rgba(12,16,28,.65);
  box-shadow: inset 0 0 24px rgba(0,255,255,.15), 0 0 0 2px rgba(120,255,255,.35), 0 10px 26px rgba(0,0,0,.35);
}

/* ---------------------------------------------------------
   NEON SCORE HUD (fixed, just left of the toggle)
   --------------------------------------------------------- */
.arcade-hud {
  position: fixed !important;
  top: 12px !important;
  right: 180px !important; /* gap from toggle */
  left: auto !important;
  bottom: auto !important;
  z-index: 2147483000;
  pointer-events: none;
}
.score-hud {
  pointer-events: auto;
  position: relative;
  min-width: 200px;
  height: 78px;
  padding: 12px 16px;
  border-radius: 16px;
  backdrop-filter: blur(6px); -webkit-backdrop-filter: blur(6px);
  background: linear-gradient(180deg, rgba(16,18,30,0.75), rgba(11,14,28,0.7));
  box-shadow: 0 0 0 1px rgba(120,255,255,.25), 0 10px 30px rgba(0,0,0,.35), inset 0 0 40px rgba(0,255,255,.06);
  border: 1px solid rgba(120,255,255,.35);
  overflow: hidden;
}
.score-hud .score-glow { position:absolute; inset:-40% -20% auto -20%; height:180%; background:radial-gradient(circle at 70% 0%, rgba(0,240,255,.35), rgba(180,0,255,.25) 40%, transparent 65%); filter:blur(20px); opacity:.55; pointer-events:none; }
.score-label { font-family:"Press Start 2P","Pixelify Sans","VT323",monospace; font-size:10px; letter-spacing:.08em; text-transform:uppercase; color:#a5f3fc; opacity:.95; }
.score-value { font-family:"Share Tech Mono","VT323",monospace; font-size:36px; line-height:1; color:#fff; text-shadow:0 0 12px rgba(0,255,255,.55), 0 0 24px rgba(180,0,255,.35); margin-top:4px; }
.score-reset { position:absolute; top:8px; right:8px; font-size:11px; padding:6px 10px; color:#0b1020; background:linear-gradient(90deg,#67e8f9,#e879f9); border:none; border-radius:999px; cursor:pointer; box-shadow:0 3px 10px rgba(0,0,0,.35); transition:transform .12s ease; }
.score-reset:hover { transform: translateY(-1px) scale(1.03); }
.score-hud.is-pulse { animation: score-bump .45s cubic-bezier(.2,.9,.2,1); }
@keyframes score-bump { 0% { transform:scale(1) } 30% { transform:scale(1.06) } 100% { transform:scale(1) } }
.score-hud.is-glitch .score-value { animation: score-glitch .35s steps(8,end); }
@keyframes score-glitch { 0%{filter:hue-rotate(0)}40%{filter:hue-rotate(20deg)}60%{filter:hue-rotate(-20deg)}100%{filter:hue-rotate(0)} }
.score-floater { position:absolute; left:18px; bottom:10px; font-family:"Share Tech Mono",monospace; font-size:16px; color:#bbf7d0; text-shadow: 0 0 10px rgba(34,197,94,.6); animation: float-up .9s ease-out forwards; pointer-events:none; }
@keyframes float-up { 0%{opacity:0; transform:translateY(8px)} 10%{opacity:1} 100%{opacity:0; transform:translateY(-22px)} }
.score-particles { position:absolute; inset:0; pointer-events:none; }
.particle { position:absolute; left:72px; top:42px; width:6px; height:6px; border-radius:2px; background:hsl(var(--hue) 100% 60%); box-shadow:0 0 10px hsla(var(--hue) 100% 60%/.75); transform:translate(0,0) scale(var(--scale)); animation: burst .9s cubic-bezier(.2,.9,.2,1) forwards; }
@keyframes burst {
  0%{opacity:1; transform:translate(0,0) rotate(0) scale(var(--scale))}
  70%{opacity:.9; transform:translate(var(--tx),var(--ty)) rotate(var(--rot)) scale(calc(var(--scale)*.9))}
  100%{opacity:0; transform:translate(calc(var(--tx)*1.15),calc(var(--ty)*1.15)) rotate(var(--rot)) scale(calc(var(--scale)*.6))}
}

/* ---------------------------------------------------------
   EFFECTS OVERLAY + BODY EFFECT CLASSES (no flash)
   --------------------------------------------------------- */
.fx-overlay { position: fixed; inset: 0; pointer-events: none; z-index: 2147482500; }
.fx-confetti-wrap { position: absolute; inset: 0; overflow: hidden; }
.fx-piece { position: absolute; width: 8px; height: 8px; border-radius: 2px; background: hsl(var(--hue,200) 90% 60%); animation: fx-fall 1.4s ease-out forwards; box-shadow: 0 0 8px rgba(255,255,255,.2); opacity: .95; }
@keyframes fx-fall { 0%{transform:translate(-10px,-10px) rotate(0); opacity:0} 10%{opacity:1} 100%{transform:translate(40px,60vh) rotate(540deg); opacity:0} }
.fx-banner { position:absolute; top:12%; left:50%; transform:translateX(-50%); background:linear-gradient(90deg,#67e8f9,#e879f9); color:#0b1020; font-weight:800; padding:10px 16px; border-radius:999px; box-shadow:0 10px 30px rgba(0,0,0,.35); animation: fx-pop .3s ease, fx-fade 2s ease .8s forwards; white-space:nowrap; letter-spacing:.03em; }
@keyframes fx-pop { 0%{transform:translateX(-50%) scale(.85); opacity:0} 100%{transform:translateX(-50%) scale(1); opacity:1} }
@keyframes fx-fade { to{opacity:0; transform:translateX(-50%) translateY(-8px)} }

/* --- Need help accordion: legend bullets fix (Arcade + Classic) --- */
/* Scope to the first "What the highlights mean" list inside the help card */
.help-card .help-section:first-of-type .help-list li {
  display: flex;            /* split pill and following text into separate items */
  align-items: flex-start;  /* avoid baseline weirdness with pixel fonts */
  column-gap: 10px;
  row-gap: 6px;
  flex-wrap: wrap;          /* allow the description to wrap below the pill */
  line-height: 1.6;
}

/* Treat the colored sample as a non-wrapping unit */
.help-card .help-section:first-of-type .help-list li
  .legend-yellow,
.help-card .help-section:first-of-type .help-list li
  .legend-red,
.help-card .help-section:first-of-type .help-list li
  .legend-blue,
.help-card .help-section:first-of-type .help-list li
  .legend-underline-gray {
  flex: 0 0 auto;
  display: inline-block;
  white-space: nowrap;      /* keep 'Hard to read' / 'Very hard to read' together */
  min-width: max-content;   /* size to content so it doesn't collapse */
  padding: 0 6px;
  line-height: 1.25;
  border-radius: 4px;
  vertical-align: middle;
}

/* The plain-text description becomes the second flex item (anonymous) and wraps nicely */

